.footer {
    border-top: 1px solid #eee;
    padding-top: 45px;
    padding-bottom: 40px;
}

.footer-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 1135px;
    margin: 0 auto;
}

.copyright {
    font-family: 'Lato', sans-serif;
    color: #9f9f9f;
    font-size: 14px;

    p {
        font-family: 'Playfair Display', serif;
        color: #000;
        font-size: 24px;
        font-weight: 600;
        margin: 0; margin-bottom: 19px;
    }
}

.designed {
    font-family: 'Lato', sans-serif;
    color: #9f9f9f;
    font-size: 14px;

    a {
        font-family: 'Lato', sans-serif;
        color: #000;
        text-decoration: none;
    }
}